<template>
  <div>
    <div class="p-3">
        <div class="row">
            <div class="offset-1 col-10">
        <p class="font-bold p-3 text-sm">Ubah Notif dan disposisi User : </p>
                  <div class="sm-form ">
            <input type="text" id="search" name="search" class="form-control form-control-sm" placeholder="cari user" v-model="search" >
             <div class="d-none">
        </div>  
          <select class='form-control' style="z-index:100;" v-model="search_skpd">
                  <option value="">-</option>
                  <option v-for="(item, index) in skpd" :key="index+'skpd'" :value="item.id_skpd" >{{item.nama_skpd}}</option>
          </select>
        </div>
            </div>
            <div class="offset-1 col-5 text-center p-2 shadow-lg  bg-blue-600 text-white font-bold ">
                USER
            </div>
            <div class="col-5 text-center p-2 shadow-lg  bg-blue-400 text-white font-bold ">
                DEPOSISI KE
            </div>
        </div>
      
        <div class="row" v-for="(item, index) in td" :key="index">
            <div class="offset-1 col-5 shadow-lg rounded-lg p-2">
                <p class="font-times text-md">Nama : {{item.nama}}</p>
                <p class="font-times text-md">Nip : {{item.nip}}</p>
                <p class="font-times text-md">Nip : {{item.unit_kerja}}</p>
            </div>
            <div class="col-5  shadow-lg rounded-lg p-2">
                <v-select :options="units" label="nama" v-model="item.atasan" @input="test(item.id,item.atasan)"></v-select>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
// let rdb = firebase.database()
import autophp from '@/plugins/autophp';
let sdb = new autophp();
export default{
  data(){
    return{
        search:"",
        search_skpd:"",
        datanya:[],
        vdata:{},
        skpd:[],
        units:[
            {
                nama:"hello"
            }
        ]
    }
  },
  computed:{
      td(){
          let data = this.datanya 
          let that = this
          data = data.filter(e=>{
              if(e.nama){
                    if(e.nama.toLowerCase().indexOf(that.search.toLowerCase())!=-1){
                      return e
                    }
                }
          })
           data = data.filter((v, i, a) => {
            let hasil = false;
            console.log(v.skpd==that.search_skpd)
              if(v['skpd']==this.search_skpd){
                return v
              }else if(this.search_skpd==''){
                return v
              }
          });
          return data
      }
  },
  methods:{
      getData(){
          sdb.collection("tbuser").doc().select(`select * from tbuser where skpd='${this.$route.query.id_skpd}'`).then(res => {
            let data = res 
            data = data.map(e=>{
                if(e.atasan!='NULL'){
                    e.atasan=JSON.parse(e.atasan)
                }
                return e
            })
            this.datanya = res
            data = data.filter(e=>{
              if(e.bagian=='deputy' || e.bagian=='unit_head'){
                return e
              }
            })
            this.units = data
        });
      },
      test(id,val){
          console.log('id',id)
          console.log(val)
          //idnya letak di vdata
          sdb.collection('tbuser').doc().set({
              id:id,
              atasan_id:val.id,
              atasan_fcm:val.fcm,
              atasan:JSON.stringify(val)
          }).then(res=>{
              console.log(res)
          })
        //   console.log(val.uid)
        //   console.log(val)
        //   db.collection('users').doc(id).set({atasan:val},{merge:true}).then(res=>{
        //       console.log('BERHASIL DI UBAH')
        //   })
      }
  },
  layout:"admin",
  mounted() {
    
    this.getData()    
    // db.collection('users').onSnapshot(res=>{
    //     let data = res.docs.map(e=>e.data())
    // })
      let that =this;
      sdb.collection("skpd").doc().select(`select * from skpd where id_skpd='${this.$route.query.id_skpd}'`).then(res => {
        // console.log(res);
        console.log('skpd',res)
        that.skpd = res
    });
  },
  metaInfo() {
      return {
        title: "Tentang TA_Maliki / Taufik Akbar Maliki",
        meta: [{  hid: "description",  name: "description",  content: "Tentang Taufik akbar maliki / TA_Maliki",},{  property: "og:title",  content: "Tentang Taufik akbar maliki / TA_Maliki",},{ property: "og:site_name", content: "TA_Maliki" },{ property: "og:type", content: "website" },{  property: `og:url`,  content: `${this.$store.state.url}`,},{  property: `og:image`,  content: `${this.$store.state.url}/Kino.jpg`,},{  property: `og:description`,  content: `documentasi ya`,},{  name: "keywords",  content: "blog,portofolio,freelance",},{ name: "robots", content: "index,follow" },],
      };
}}
</script>